import Root from './Root'
import IndexPage from './IndexPage'
import CaserView from './CaserView'
import PlayfairView from './PlayfairView'
import HillView from './HillView'
import NotFound from './NotFound'

export const innerRoutes = [
  {
    path: '/',
    exact: true,
    component: IndexPage,
  },
  {
    path: '/caser',
    component: CaserView,
  },
  {
    path: '/playfair',
    component: PlayfairView,
  },
  {
    path: '/hill',
    component: HillView,
  },
  {
    path: '*',
    component: NotFound,
  },
]

export default [
  {
    component: Root,
    routes: innerRoutes,
  },
]

export const titleMap = innerRoutes.reduce((rs, { component, path }) => {
  rs[path] = component.title // eslint-disable-line no-param-reassign
  return rs
}, {})



